* {
	margin: 0;
}

body {
	background-color: #f5f6fa;
}

.app {
	display: flex;
	justify-content: space-between;
	padding: 20px;
}

.app__dropdown {
	background-color: white;
}

.app__left {
	flex: 0.9;
}

.app__header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 20px;
}

.app__header > h1 {
	color: #fc3c3c;
	font-size: 2rem;
}

.app__stats {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 20px;
}

@media (max-width: 990px) {
	.app {
		flex-direction: column;
	}
}

.app__right {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.app__right .muiCardContent-root {
	display: flex;
	flex-grow: 1;
}

.app__right .table {
	height: 600px;
}
